.header{
	position: relative;
	padding: 15px 40px 15px 50px;
	background:#ffffff;
	box-shadow: 0 7px 8px rgba(0, 0, 0, 0.1);
	@include clearfix();
	z-index: 10000;

	@include note {
		padding: 15px;
	}
}
.header__logo{
	float: left;

	@include small {
		float: none;
		text-align: center;
	}
}
.header__content{
	float: right;
	padding-top: 15px;

	@include small {
		float: none;
		text-align: center;
	}
}
.header__phones{
	display: inline-block;
	margin-right: 40px;
	vertical-align: middle;

	@include note {
		margin-right: 25px;
	}

	@include tablet {
		display: none;
	}
}
.header__email{
	display: inline-block;
	margin-right: 40px;
	vertical-align: middle;

	@include pc {
		display: none;
	}
}
.header__langs{
	display: inline-block;
	margin-right: 40px;
	vertical-align: middle;

	@include note {
		margin-right: 25px;
	}
}
.header__search{
	display: inline-block;
	margin-right: 45px;
	vertical-align: middle;

	@include note {
		margin-right: 25px;
	}
}
.header__nav{
	display: inline-block;
	vertical-align: middle;
}