/**
 *
 * buttons
 *
 **/
.btn{
  position: relative;
  display: inline-block;
  border-radius: 10px;
  border: 1px solid #ffffff;
  background-color: rgba(#ffb600, .8);
  box-shadow: 0 2px 6px 1px rgba(0, 0, 0, 0.25);
  padding: 12px 38px;
  font-size: 14px;
  font-weight: 300;
  line-height: 25px;
  color: #505050;
  text-transform: uppercase;
  text-decoration: none;
  transition: all .3s;
  &:hover {
    text-decoration: none;
    background-color: rgba(#ffb600, .5);
  }

  i {
    position: absolute;
    right: 15px; top: 16px;
    color: #794221;
  }
}