.features{
	position: relative;
	min-height: 850px;
}
.features__title{
	margin-bottom: 100px;
	text-align: center;
	text-decoration: none;
	&:hover{
		text-decoration: none;
	}
}
.features__img{
	text-align: center;

	@include note {
		display: none;
		// position: absolute;
		// left: 0;
		// right: 0;
		// width: 100%;
	}
}

.features__item{
	position: absolute;
	max-width: 300px;

	@include note {
		position: static;
		display: inline-block;
		vertical-align: top;
		width: percentage(1/2);
		max-width: 100%;
		padding: 0 15px;
		margin-bottom: 50px;
		margin-right: -.25em;
	}

	@include small {
		display: block;
		width: 100%;
		margin: 0 0 40px;
	}
}
.features__item--1{
	top: 165px;
	left: 70px;
}
.features__item--2{
	bottom: 90px;
	left: 70px;
}
.features__item--3{
	bottom: 0;
	left: 40%;
}
.features__item--4{
	top: 165px;
	right: 50px;
	width: 100%;

	@include pc {
		right: 100px;
		width: 50%;
	}
	@include small {
		width: 100%;
	}
}
.features__item--5{
	top: 400px;
	right: 0;
}
.features__item--6{
	bottom: 90px;
	right: 200px;

	@include pc {
		right: 100px;
	}
}
.features__item-title{
	display: block;
	position: relative;
	margin: 0 0 35px;
	padding-bottom: 8px;
	font-size: 41px;
	font-weight: 900;
	line-height: 40px;
	color: #262628;

	@include tablet {
		font-size: 28px;
		line-height: 32px;
		margin-bottom: 20px;
	}

	&:after {
		position: absolute;
		bottom: 0; left: 0;
		content: '';
		width: 176px;
		height: 2px;
		box-shadow: 0 8px 7px rgba(7, 7, 7, 0.31);
		background-color: #ffca43;

		@include tablet {
			width: 90%;
		}
		@include small {
			width: 60%;
		}
	}
	&:hover{
		text-decoration: none;
	}
}
.features__item-title--alt{ color: #ff2e50; }
.features__item-list{
	margin: 0;
	padding: 0;
	list-style: none;

	li {
		opacity: 0.91;
		font-size: 16px;
		font-weight: 500;
		line-height: 24px;
		color: #1d1d1d;
	}
	a {
		display: block;
		text-decoration: none;
		&:hover{
			text-decoration: none;
		}
	}
}