.testimonial{
	text-align: center;
}
.testimonial__author{
	img {
		display: block;
		margin: 0 auto 15px;
		max-width: 79px;
		border-radius: 50%;
	}
	strong,
	span{
		display: block;
		font-size: 18px;
		font-weight: 600;
		line-height: 21px;
		color: rgba(#000000,.4);
	}
	span{
		margin-bottom: 35px;
		font-style: italic;
		font-weight: 300;
	}
}
.testimonial__text{
	padding: 0 0 25px;
	font-size: 16px;
	font-weight: 500;
	font-style: italic;
	line-height: 24px;
	color: rgba(#000000,.7);
}
.testimonial__rating{
	text-align: center;
	padding: 25px 0;
}
.testimonial__rating-stars{
	display: inline-block;
	overflow: hidden;
	width: 134px;
	height: 22px;
	background:transparent url('../images/stars.png') -134px top no-repeat;

	&.is--1 { background-position: -107px 0; }
	&.is--2 { background-position: -80px 0; }
	&.is--3 { background-position: -53px 0; }
	&.is--4 { background-position: -26px 0; }
	&.is--5 { background-position: 0 0; }
}