.cost{
	border-radius: 5px;
	background-color: rgba(#f2b826, .3);
	text-align: center;
}
.cost__header{
	padding: 15px 20px;
	border-radius: 5px 5px 0 0;
	background-color: #f2b826;

	strong{
		line-height: 20px;
		font-size: 22px;
		font-weight: 900;
		color: #313131;
	}
	span{
		font-size: 16px;
		font-weight: 300;
		line-height: 20px;
	}
}
.cost__body{
	padding: 15px 20px;
	font-size: 16px;
	font-weight: 300;
	line-height: 20px;
	color: #2d2d2d;
}