*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-width: 320px;
  width: 100%;
  font-family: "Museo Sans", Arial, sans-serif;
  font-weight: 500;
  font-size: $fs--base;
  line-height: $lh--base;
  color: $color__base;
}
a {
  text-decoration: none;
  color: $color__secondary;
  @extend %transition;
  transition-property: color;
  
  &:hover {
    text-decoration: underline;
  }
}
h3, .h3{
  margin: 0 0 25px;
  font-size: 26px;
  font-weight: 900;
  line-height: 21px;
  color: #000000;
}
p {
  margin: 0 0 25px;
  font-size: 16px;
  font-weight: 300;
  line-height: 21px;
  color: #000000;
}
img {
  max-width: 100%;
}
table{
  width: 100%;
  tr td {
    padding: 15px 20px 15px 0;
    border-bottom: 1px solid #e5e5e5;
  }
}
.spacer{
  height: 50px;
}
.gold-line{
  width: 100%;
  height: 1px;
  margin-bottom: 25px;
  opacity: 0.5;
  background-color: #f3af07;
}
.row {
  list-style: none;
  margin-left: -30px
}
.col {
  box-sizing: border-box;
  display: inline-block;
  margin-right: -.25em;
  min-height: 1px;
  padding-left: 30px;
  vertical-align: top
}

.container{
  min-width: 320px;
  max-width: 1170px;
	margin: 0 auto;
  padding: 0 15px;
  @include clearfix();
  
  // @include pc {
  //   max-width: 970px;
  // }

  // @include note {
  //   max-width: 750px;
  // }

  // @include tablet {
  //   max-width: 480px;
  // }

  // @include small {
  //   max-width: 90%;
  // }

  // @include phone {
  //   width: 100%;
  // }

}
.title{
  margin:0 0 20px;
  font-size: 25px;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}

// Error State For Form Elements

label{
  &.error{
    display: block;
    text-align: right;
    font-size: $fs--small;
    line-height: $lh--small;
    color: $color__secondary;
  }
}

input, 
select, 
textarea {
  &.error{
    border-color: $color__secondary;
    color: $color__secondary;
  }
}

img.is--fl{
  display: block;
  float: left;
  margin: 0 15px 15px 0;

  @include tablet {
    float: none;
    margin: 0 auto 15px;
  }
}

h3 {
  margin: 0 0 15px;
}
p, .text {
  margin: 0 0 15px;
  font-size: 14px;
  line-height: 21px;
}
.text--gray{
  color: rgba(#313236, .6);
}
.text--italic{
  font-style: italic;
}