.slider{}
.slider__item{}
.slider__content,
.slider__photo{
  &,&:hover { text-decoration: none; }
}
.slider__content{
  padding-top: 70px;
  padding-left: 100px;
  display: inline-block;
  width: 55%;
  vertical-align: middle;
  margin-right: -.25em;
  text-transform: uppercase;
  text-shadow: -1px 17px 18px rgba(0, 0, 0, 0.74);

  @include note {
    display: block;
    width: 100%;
    padding: 100px 140px;
    text-align: center;
  }
  @include tablet {
    padding: 40px 20px;
  }
}
.slider__photo{
  display: inline-block;
  width: 45%;
  vertical-align: middle;
  margin-right: -.25em;
  padding-right: 80px;

  @include note {
    display: none;
  }
}
.slider__line-1{
  font-size: 26px;
  font-weight: 300;
  line-height: 30px;
  color: #e9e9e9;

  @include pc {
    font-size: 20px;
    line-height: 24px;
  }
  @include tablet {
    font-size: 16px;
    line-height: 20px;
  }
}
.slider__line-2{
  font-size: 42px;
  font-weight: 700;
  line-height: 43px;
  color: #e9e9e9;

  @include pc {
    font-size: 32px;
    line-height: 38px;
  }
  @include tablet {
    font-size: 24px;
    line-height: 28px;
  }
}
.slider__line-3{
  margin: 20px 0;  
  span{
    display: inline-block;
    vertical-align: bottom;
    font-size: 32px;
    font-weight: 300;
    line-height: 32px;
    color: #ffffff;

    @include pc {
      font-size: 24px;
      line-height: 28px;
    }
    @include tablet {
      font-size: 16px;
      line-height: 20px;
    }
  }
  strong{
    display: inline-block;
    vertical-align: bottom;
    font-size: 42px;
    font-weight: 900;
    line-height: 42px;
    color: #ffffff;

    @include pc {
      font-size: 36px;
      line-height: 38px;
    }
    @include tablet {
      font-size: 24px;
      line-height: 28px;
    }
  }
}

.slider--promo{
  position: relative;
  .promo__controls{
    position: absolute;
    top: 50%; left: 0; right: 0;
    margin-top: -26px;
    width: 100%;

    a {
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
  .promo__prev{
    position: absolute;
    left: 40px; top: 0;
    cursor: pointer;
  }
  .promo__next{
    position: absolute;
    right: 40px; top: 0;
    cursor: pointer;
  }
}
.slider--apartments{
  position: relative;

  .apartments__pagination {
    height: 60px;
    box-shadow: 0 18px 25px 2px rgba(0, 0, 0, 0.13);
    border-radius: 10px;
    background-color: #ffffff;
    list-style-type: none;
    margin: 0 0 65px;
    padding: 20px 40px;
    text-align-last: justify;
    
    @include note {
      padding: 20px 10px;
    }
    @include tablet {
      height: auto;
    }

    li { 
      display: inline-block; 
        
      @include tablet {
        display: block;
      }
    }
    a { 
      font-size: 16px;
      font-weight: 300;
      line-height: 20px; 
      color: #4c4c4c;
      text-decoration: none;

      @include note {
        font-size: 14px;
      }
      
      @include tablet {
        display: block;
        padding: 5px 10px;
        text-align: center;
        text-align-last: center;
      }

      &:hover,
      &.active { color: #ef8117; text-decoration: none; }
    }
  }
  .apartments__controls{
    position: absolute;
    top: 50%; left: 0; right: 0;
    margin-top: -26px;
    width: 100%;

    a {
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
  .apartments__prev{
    position: absolute;
    left: 40px; top: 0;
    cursor: pointer;
    opacity: .8;

    @include pc {
      left: -10px;
    }
  }
  .apartments__next{
    position: absolute;
    right: 40px; top: 0;
    cursor: pointer;
    opacity: .8;

    @include pc {
      right: -10px;
    }
  }
}
.slider--apartments-page{
  position: relative;
  visibility: hidden;

  .apartments-page__pagination {
    height: 60px;
    box-shadow: 0 18px 25px 2px rgba(0, 0, 0, 0.13);
    border-radius: 10px;
    background-color: #ffffff;
    list-style-type: none;
    margin: 0 0 65px;
    padding: 20px 40px;
    text-align-last: justify;

    @include note {
      padding: 20px 10px;
    }
    @include tablet {
      height: auto;
    }

    li { 
      display: inline-block;  
        
      @include tablet {
        display: block;
        text-align-last: left;
      }
    }
    a { 
      font-size: 16px;
      font-weight: 300;
      line-height: 20px; 
      color: #4c4c4c;
      text-decoration: none;

      @include note {
        font-size: 14px;
      }
      
      @include tablet {
        display: block;
        padding: 5px 10px;
        text-align: center;
        text-align-last: center;
      }

      &:hover,
      &.active { color: #ef8117; text-decoration: none; }
    }
  }
  .apartments-page-slider__thumbs{
    @include clearfix();
    padding: 20px 70px 45px;
    a {
      float: left;
      width: 100px;
      margin-right: 10px;
    }
    img {
      display: block;
      margin: 0 auto;
    }
  }
  .apartments-page-slider__controls{
    position: absolute;
    top: 0; left: 0; right: 0;

    a {
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
  .apartments-page-slider__prev,
  .apartments-page-slider__next{
    position: absolute;
    top: 0;
    width: 50px;
    height: 400px;
    line-height: 400px;
    background-color: rgba(#000, 0);
    transition: all .3s;
    text-align: center;
    cursor: pointer;
    &:hover {
      background-color: rgba(#000, .4);
    }
  }
  .apartments-page-slider__prev{
    left: 0;
  }
  .apartments-page-slider__next{
    right: 0;
  }
}
.slider--propose{
  position: relative;

  .bx-wrapper{
    @include huge {
      max-width: 1150px !important;
    }
    @include pc {
      max-width: 860px !important;
    }
    @include note {
      max-width: 565px !important;
    }
    @include small {
      max-width: 265px !important;
    }
  }

  .propose__controls{
    position: absolute;
    top: 50px; left: 0; right: 0;
    width: 100%;

    a {
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
  .propose__prev{
    position: absolute;
    left: 40px; top: 0;
    cursor: pointer;
  }
  .propose__next{
    position: absolute;
    right: 40px; top: 0;
    cursor: pointer;
  }
}

.slider--partners{
  margin-bottom: 70px;
}

.slider--testimonial{
  position: relative;
  padding: 0 40px 120px;

  .testimonial__controls{
    position: absolute;
    top: 175px; left: 0; right: 0;
    width: 100%;

    a {
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
  .testimonial__prev{
    position: absolute;
    left: 0; top: 0;
    cursor: pointer;
  }
  .testimonial__next{
    position: absolute;
    right: 0; top: 0;
    cursor: pointer;
  }

  .testimonial__pagination{
    font-size: 0; line-height: 0;
    text-align: center;
    a {
      display: inline-block;
      margin-right: 10px;
      width: 11px;
      height: 11px;
      border-radius: 50%;
      background-color: #d3d3d3;
      vertical-align: middle;
      cursor: pointer;
    }
    a.active{ background-color: #343434; }
  }
}