.breadcrumbs{
  margin: 0; padding: 0;

  &.is--absolute {
    position: absolute; 
    left: 0; right: 0;
    width: 100%;
    padding-top: 20px;
    z-index: 20;

    @include media('<=992px'){
      position: relative;
    }
  }
  
  @include media('<=768px'){
    margin-bottom: 20px;
  }
	a {
		text-decoration: none;
    color: #dedede;
	}
	a:hover{
    color: #ef8117;
  }
}
.breadcrumbs__item{
  position: relative;
  display: inline-block;
  padding-left: 12px;
  margin-left: 4px;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 300;
  line-height: 30px;
  color: #dedede;

	&:first-child{
    padding-left: 4px;
    margin-left: 0;
	}
	&:first-child:before{
		display: none;
	}

	&:before{
		content: '/';
		position: absolute;
    left: 0px; top:1px;
    color: #dedede;
	}
}