.main{
	position: relative;
	padding: 60px 0;
	background:transparent url('../images/page-bg.png') left top repeat;
}
.main--contacts{
	padding-top: 325px;
	background:transparent url('../images/page-top-bg.jpg') center -100px no-repeat;

	@include note {
		padding-top: 200px;
	}
}
.main--comments{
	padding-top: 325px;
	background:transparent url('../images/page-top-bg.jpg') center -100px no-repeat;

	@include note {
		padding-top: 200px;
	}
}
.main--content{
	padding-top: 325px;
	background:transparent url('../images/page-top-bg--1.jpg') center -100px no-repeat;

	@include note {
		padding-top: 200px;
	}
}
.main__title {
	margin: 0 0 30px;
	font-size: 60px;
	font-weight: 900;
	line-height: 76px;
	text-transform: uppercase;
	text-align: center;
	color: #000000;
}
.main__text{
	position: relative;
	margin: 0 auto 70px;
	max-width: 800px;
	font-size: 14px;
	line-height: 21px;
	font-weight: 300;
	text-align: center;
	color: #1c1c1c;
	opacity: 0.7;

	&.with--border-bottom{
		padding-bottom: 20px;
		&:after {
			content: '';
			position: absolute;
			bottom: 0; left: 0; right: 0;
			margin: 0 auto;
			width: 176px;
			height: 2px;
			box-shadow: 0 8px 7px rgba(7, 7, 7, 0.31);
			background-color: #ffca43;
		}
	}
}