//.apartment-page{}
.apartment-page__inner{
	background-color: #ffffff;
	overflow: hidden;
}
//.apartment-page__photo{}
.apartment-page__content{
	padding: 20px 70px 70px;
}
.apartment-page__title{
	display: inline-block;
	vertical-align: middle;
	width: percentage(1/3);
	margin-right: -.25em;
	margin: 0 0 20px;
	font-size: 36px;
	font-weight: 900;
	line-height: 40px;
	color: #000;

	@include note {
		width: percentage(1/1);
		margin-bottom: 10px;
		font-size: 24px;
		line-height: 28px;
	}
}
.apartment-page__price{
	display: inline-block;
	vertical-align: middle;
	width: percentage(1/3);
	margin-right: -.25em;
	margin-bottom: 20px;
	font-size: 24px;
	font-weight: 100;
	line-height: 30px;
	color: #717171;

	@include note {
		width: percentage(1/1);
		margin-bottom: 10px;
	}
}
.apartment-page__action{
	display: inline-block;
	vertical-align: middle;
	width: percentage(1/3);
	margin-right: -.25em;
	margin-bottom: 20px;
	text-align: right;
	
	@include note {
		width: percentage(1/1);
		margin-bottom: 25px;
		text-align: left;
	}
}
.apartment-page__list{
	margin: 0 0 30px;
	padding: 0 18px 0 0;
	list-style: none;
}
.apartment-page__list-item{
	display: block;
	overflow: hidden;
	strong{
		float: right;
    width: 50%;
		font-size: 14px;
		font-weight: 500;
		line-height: 22px;
		color: #2b2b2b;
	}
	span{
		float: left;
    width: 50%;
		font-size: 14px;
		font-weight: 100;
		line-height: 22px;
		color: #888888;
	}
}