.booking-result{
	display: inline-block;
	max-width: 360px;
	padding: 30px;
	border-radius: 5px;
	border: 1px solid #f9ba48;
	background: #ffffff url('../images/lines-bg.png') 0 0 repeat;
	@include clearfix();
	text-align: left;

	@include note {
		width: 100%;
		max-width: 100%;
		padding: 20px 15px;
	}
}
.booking-result__state{
	margin: 0 0 5px;
	font-size: 16px;
	font-weight: 500;
	line-height: 23px;
	color: #f6a32c;
}
.booking-result__title{
	margin: 0 0 30px;
	font-size: 29px;
	font-weight: 700;
	line-height: 30px;
	text-transform: uppercase;
	color: #313236;
	
	@include note {
		margin-bottom: 20px;
		font-size: 21px;
		line-height: 28px;
	}
}
.booking-result__body{
	margin-bottom: 25px;
}
.booking-result__list{
	margin: 0;
	padding: 0;
	list-style-type: none;
}
.booking-result__item{
	padding-bottom: 12px;
	overflow: hidden;

	span {
		float: left;
		width: 80px;
		font-size: 16px;
		font-weight: 500;
		line-height: 20px;
		color: rgba(#313236, .6);
	}
	strong {
		display: block;
		margin-left: 90px;
		font-size: 16px;
		font-style: italic;
		line-height: 20px;
		color: #313236;
	}
}
.booking-result__action{
	text-align: center;
}