.propose{
	margin-bottom: 50px;
	text-align: center;
}
.propose__photo{
	display: block;
	margin-bottom: 20px;

	img {
		display: block;
		max-height: 150px;
		margin: 0 auto;
		box-shadow: 0 12px 10px rgba(0, 0, 0, 0.23);
		border-radius: 5px;
	}
}
.propose__title{
	display: block;
  margin: 0 0 8px;
	a {
		font-weight: 700;
		font-size: 18px;
		line-height: 21px;
		display: block;
		color: #2c2c2c;
	}
}
.propose__details{
  overflow: hidden;
  max-height: 42px;
	font-size: 14px;
	font-weight: 300;
	font-style: italic;
	line-height: 14px;
	color: #494949;
}