.section{
	position: relative;
	padding: 40px 0;
	background:#fff;
}
.section__title{
	display: block;
	margin: 0 0 30px;
	font-size: 38px;
	font-weight: 900;
	line-height: 40px;
	text-transform: uppercase;
	color: #2c2c2c;
	text-align: center;
}
.section__text{
	max-width: 810px;
	margin: 0 auto 50px;
	opacity: 0.7;
	font-size: 14px;
	font-weight: 500;
	line-height: 21px;
	text-align: center;
	color: #000000;
}
.section--video{
	overflow: hidden;
	padding: 0;
	background:transparent;
}
.section--features{
	min-height: 882px;
	background:transparent url('../images/section/features.jpg') center bottom no-repeat;
}
.section--about{
	min-height: 660px;
	background:transparent url('../images/section/about.jpg') right bottom no-repeat;
}
.section--appartments{
	min-height: 660px;
	background:transparent url('../images/section/appartments.jpg') left top no-repeat;
}
.section--propose{
	min-height: 505px;
	background:transparent url('../images/section/propose.jpg') center center repeat;
}
.section--partners{
	min-height: 800px;
	background:transparent url('../images/section/partners.jpg') left center no-repeat;
}
.section--seo{
	min-height: 800px;
	background:transparent url('../images/section/seo.jpg') right top no-repeat;
}