.booking{
	display: inline-block;
	max-width: 311px;
	padding: 40px 30px 30px;
	border-radius: 5px;
	border: 1px solid #f9ba48;
	background: #ffffff url('../images/lines-bg.png') 0 0 repeat;
	text-align-last: auto;
	@include clearfix();

	@include huge {
		position: absolute;
		bottom: 20px;
		left: 0;
		right: 0;
		display: block;
		max-width: 480px;
		padding: 20px 15px;
		margin: 0 auto;
	}
	@include small {
		position: relative;
		max-width: 240px;
	}
}

@media screen and (max-height: 700px) {
	.booking {
		position: relative;
		margin-top: 40px;
	}
}
.booking__state{
	margin: 0 0 10px;
	font-size: 16px;
	font-weight: 500;
	line-height: 23px;
	color: #f6a32c;
}
.booking__title{
	margin: 0 0 40px;
	font-size: 29px;
	font-weight: 700;
	line-height: 30px;
	text-transform: uppercase;
	color: #313236;

	@include huge {
		font-size: 26px;
		line-height: 28px;
		margin-bottom: 15px;
	}
}
.booking__body{
	margin-bottom: 50px;
	text-align: center;

	@include huge {
		float: left;
		margin-bottom: 0;
	}
}
.booking__date{
	display: inline-block;
	margin-bottom: 10px;
	margin-right: -.24em;
	margin-left: 15px;

	@include huge {
		margin-bottom: 0;
	}

	&:first-child{
		margin-left: 0;
	}
}
.booking__date-inner{
	padding: 10px 20px;
	width: 117px;
	height: 117px;
	border-radius: 5px;
	border: 1px solid #545c62;
	vertical-align: top;

	@include huge {
		width: 115px;
		height: 90px;
		padding: 10px;
	}
}
.booking__mm-yyyy{
	margin-bottom: 5px;
	font-size: 16px;
	font-weight: 500;
	line-height: 23px;
	color: #f6a32c;
}
.booking__dd{
	font-size: 68px;
	font-weight: 900;
	line-height: 70px;
	color: #545c62;

	@include huge {
		font-size: 38px;
		line-height: 40px;
	}
}
.booking__text{
	font-size: 13px;
	font-weight: 300;
	line-height: 19px;
	color: rgba(#303437, .5);
}
.booking__guarantee{
	margin-bottom: 15px;
	text-align: center;	
}
.booking__action{
	text-align: center;
}

.booking.is--widget{
	position: relative;
	top: 0; bottom: 0;
	width: 100%;
	padding: 20px 20px 30px;
	margin-bottom: 50px;

	.booking__title{
		font-size: 24px;
		line-height: 30px;
	}

	.booking__body{
		margin-bottom: 30px;
	}
	.booking__date-inner{
		padding: 10px 5px;
		width: 90px;
		height: 90px;
		border-radius: 5px;
		background:#fff;

		@include huge {
			width: 90px;
			height: 90px;
			padding: 10px 5px;
		}
	}
	.booking__mm-yyyy{
		margin-bottom: 2px;
		font-size: 16px;
		font-weight: 500;
		line-height: 20px;
	}
	.booking__dd{
		font-size: 50px;
		line-height: 50px;

		@include huge {
			font-size: 38px;
			line-height: 40px;
		}
	}
	.booking__action{
		position: absolute;
		left: 0; right: 0;
	}
}


@media screen and (max-height: 700px) {
	.booking.is--widget {
		margin-top: 0;
	}
}