.tabs{}
.tabs__list{
	margin: 0;
	padding: 0;
	list-style-type: none;
	@include clearfix();

	li{
		float: left;
		margin-right: 20px;

		@include pc {
			margin-right: 10px;
		}
		@include note {
			float: none;
			width: 100%;
		}

		a{
			position: relative;
			display: block;
			padding: 15px 20px;
			border-radius: 5px 5px 0 0; 
			font-size: 16px;
			font-weight: 700;
			line-height: 20px;
			background-color: #ffffff;
			color: #6f6f6f;
			text-transform: uppercase;
				
			@include pc {
				font-size: 14px;
				padding: 10px 15px;
			}
			@include note {
				border-radius: 0;
			}

			&:after{
				content: '';
				position: absolute;
				left: 0; right: 0;
				bottom: 0;
				width: 0;
				height: 0;
				margin: 0 auto;
				background:#fff;
				transition: all .2s cubic-bezier(0.39, 0.58, 0.57, 1);
				z-index: 10;
			}
		}

		a:hover {
			text-decoration: none;

			&:after{
				width: 100%;
				height: 2px;
				bottom: -2px;
			}
		}

		&.is--active a{
			background-color: #f2b826;
			color: #ffffff;
		}
	}
}
.tabs__content{
	display: none;

	&.is--active{
		display: block;
	}
}