.apartment{
	max-width: 974px;
	padding: 0 15px 50px;
}
.apartment__inner{
	margin: 0 auto;
	padding: 16px 14px;
	box-shadow: 0 25px 33px 2px rgba(0, 0, 0, 0.14);
	background-color: #ffffff;
	overflow: hidden;
}
.apartment__photo{
	float: left;
	width: percentage(6/10);

	@include note {
		width: percentage(10/10);
		
	}
}
.apartment__content{
	float: left;
	padding-top: 15px;
	padding-left: 25px;	
	width: percentage(4/10);
	@include note {
		width: percentage(10/10);
	}
}
.apartment__title{
	display: block;
	margin: 0 0 15px;
	color: #515151;
	font-size: 36px;
	font-weight: 900;
	line-height: 40px;

	@include note {
		font-size: 24px;
		line-height: 28px;
	}
}
.apartment__price{
	margin-bottom: 20px;
	font-size: 14px;
	font-style: italic;
	line-height: 20px;
	color: #ef8117;
}
.apartment__properties{
	margin: 0 0 30px;
	padding: 0 18px 0 0;
	list-style: none;
}
.apartment__properties-item{
	display: block;
	overflow: hidden;
	strong{
		float: left;
		width: 120px;
		font-size: 14px;
		font-weight: 500;
		line-height: 22px;
		color: #2b2b2b;
    max-width: 50%;
	}
	span{
		float: right;
		font-size: 14px;
		font-weight: 100;
		line-height: 22px;
		color: #888888;
    max-width: 50%;
    text-align: right;
	}
}
.apartment__action{
	text-align: right;
}