.video{
	position: relative;
	background: transparent;
}

.ytplayer-container{
	position: absolute;
	top: 0;
	z-index: -1;
	&:after{
		content: '';
		position: absolute;
		top: 0; right: 0; bottom: 0; left: 0;
		height: 100%; width: 100%;
		background: url('../images/video-overlay.png') 0 0 repeat;
		z-index: 20;
	}
}
