.comment{
	&.is--answer{
		padding-left: 30px;
	}
}
.comment__header{
	margin-bottom: 15px;
	time {
		display: inline-block;
		vertical-align: bottom;
		font-size: 10px;
		font-style: italic;
		line-height: 21px;
		color: #8d8d8d;
	}
	time strong {
		font-weight: 400;
	}
	time span {
		margin-left: 10px;
	}
}
.comment__author{
	display: inline-block;
	vertical-align: bottom;
	margin: 0 29px 0 0;
	font-size: 26px;
	font-weight: 900;
	line-height: 26px;
	color: #000000;
}
.comment__body{
	margin-bottom: 35px;
	font-size: 14px;
	font-weight: 300;
	line-height: 21px;
	color: #686868;

	p {
		margin: 0;
	}
}