.booking-block{
  @include clearfix();
}
.booking-block__form{
  float: left;
  width: percentage(6/10);

  @include note {
    float: none;
    width: percentage(10/10);
    margin-bottom: 30px;
  }
}
.booking-block__details{
  float: right;
  width: percentage(4/10);
  padding-top: 20px;
  text-align: right;
  
  @include note {
    float: none;
    width: percentage(10/10);
    text-align: left;
  }
}