.card{
	display: inline-block;
	margin-right: -.25em;
	transition: transform .3s;
	text-decoration: none;
	&:hover{
		text-decoration: none;
	}
	&.active {
		transform: translateY(-20px);
	}
}
.card__inner{
	position: relative;
	padding: 30px 15px 0;
}
.card__photo{
	position: absolute;
	left: 0; bottom: 0;
	img{ display: block; }
}
.card__content{
	position: relative;
	width: 400px;
	height: 120px;
	padding: 20px 30px 0 170px;
	border-radius: 5px;
	background-color: #e6e6e8;
	box-shadow: 0 49px 32px rgba(0, 0, 0, 0.49);
	text-align-last: auto;
	z-index: -1;
}
.card__title{
	margin: 0 0 10px;
	font-size: 20px;
	font-weight: 700;
	line-height: 20px;
	color: #313030;
}
.card__text{
	margin: 0;
	font-size: 14px;
	font-weight: 100;
	line-height: 18px;
	color: #4b4b4b;
}