.promo{
	@include clearfix();
}
.promo__left{
	float: left;
	width: 75%;
	padding-right: 40px;

	@include huge {
		float: none;
		width: 100%;
		padding: 0 0 10px;
	}
}
.promo__right{
	float: right;
	width: 25%;

	@include huge {
		float: none;
		width: 100%;
	}
}
.promo__bottom{
	padding-top: 40px;
	clear: both;
}

.promo__pagination{
  padding: 0 80px;
  text-align-last: justify;
}

@media screen and (max-width: 1879px) {
  .promo__pagination{
    display: none;
  }
}
@media screen and (max-height: 859px) {
  .promo__pagination{
    display: none;
  }
}