.block{
	padding: 35px 50px;
	border-radius: 0 0 5px 5px;
	background-color: #ffffff;

	@include note {
		padding: 20px 30px;
	}
	@include tablet {
		padding: 20px 15px;
	}

	&.with--shadow{
		box-shadow: 0 6px 13px rgba(0, 0, 0, 0.38);
	}
	&.with--bg{
		background:#fff url('../images/menu-bg-top.png') right top no-repeat;
		background-size: contain;
	}

	&.is--border-top-primary {
		border-top: 2px solid #f4af0c;
	}
	&.is--no-padding{
		padding: 0;
	}
}
.block__title{
	margin: 0 0 35px;
	font-size: 54px;
	font-weight: 900;
	line-height: 60px;
	color: #36332e;

	@include note {
		font-size: 42px;
		line-height: 48px;
	}
	@include small {
		font-size: 36px;
		line-height: 40px;
	}
}