.about{}
.about__inner{
	@extend .row;

	@include tablet {
		display: block;
		margin: 0;
	}

}
.about__photo{
	@extend .col;
	width: percentage(4/12);
	text-align: right;

	@include tablet {
		display: none;
		// width: percentage(12/12);
		// text-align: left;
	}

	img {
		box-shadow: 12px 21px 21px rgba(0, 0, 0, 0.27);
	}
}
.about__content{
	@extend .col;
	padding-left: 60px;
	width: percentage(8/12);

	@include tablet {
		width: percentage(12/12);
		padding-left: 0;
	}
}
.about__title{
	margin: 0 0 35px;
	font-size: 38px;
	font-weight: 900;
	line-height: 40px;
	text-transform: uppercase;
	text-align: left;
	color: #000000;
}
.about__text{
	margin-bottom: 30px;
	font-size: 14px;
	line-height: 21px;
	color: #000;
	font-weight: 300;

	p {
		margin: 0 0 30px;
	}
}
.about__icons{
	@extend .row;
}
.about__icons-item{
	@extend .col;
	width: percentage(1/4);
	text-align: center;

	@include small {
		width: percentage(1/2);
		margin-bottom: 30px;
	}

	i{
		margin-bottom: 20px;
	}
	span{
		display: block;
		font-size: 22px;
		font-weight: 700;
		line-height: 30px;
		text-align: center;
		color: #000000;
	}
}