.page{}
.page__header{
	position: absolute;
	left: 0; right: 0; top: 140px;
	margin: 0;
	padding: 0;
	font-size: 60px;
	font-weight: 900;
	line-height: 76px;
	text-shadow: 0 6px 18px rgba(0, 0, 0, 0.71);
	text-transform: uppercase;
	text-align: center;
	color: #ffffff;

	@include note {
		font-size: 48px;
		line-height: 54px;
	}
	@include tablet {
		font-size: 36px;
		line-height: 40px;
	}
	@include small {
		top: 70px;
		font-size: 28px;
		line-height: 32px;
	}
}