.langs{}
.langs__item{
	position: relative;
	display: inline-block;
	vertical-align: middle;

	font-size: 18px;
	line-height: 20px;
	font-weight: 100;
	color: #a2a2a2;

	&:before{
		display: inline-block;
		padding: 0 5px;
		content: '/';
		font-weight: 100;
		color: #a2a2a2;
	}

	&:first-child:before{
		display: none;
	}

	&.is--current{
		color: #3e4143;
		font-weight: 700;
	}
}
